<template>
  <div class="release-house-resource">
    <el-row>
      <el-col class="col-contianer" :span="12">
        <el-form v-model="HouseResource" label-width="80px">
          <template v-for="item in HouseResource">
            <el-form-item
              :key="item.label"
              :label="item.label"
              :prop="item.propName"
            >
              <component
                :disabled="item.disabled"
                :is="item.componentName"
                :class="item.customClass"
                v-model="HouseResource[item.propName]"
                size="small"
              >
                <template slot="append" v-if="item.slot">{{
                  item.slot
                }}</template>
              </component>
            </el-form-item>
          </template>
          <el-upload
            class="uploadimg"
            ref="uploader"
            action="/api/users/uploadAvator"
            list-type="picture-card"
            :headers="headerObj"
            name="file"
            multiple
            show-file-list
            :file-list="fileList"
            :with-credentials="true"
            :auto-upload="false"
            :on-change="handleChange"
            :on-success="handleUploadSuccess"
            :before-upload="beforeUploadUpload"
          >
            <el-button slot="trigger" size="small" type="primary"
              >选取图片</el-button
            >
          </el-upload>
          <el-form-item>
            <el-button type="primary" size="small" @click="submitForm"
              >提交</el-button
            >
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["houselabels"]),
  },
  data() {
    return {
      headerObj: {
        Authorization: "Bearer " + window.sessionStorage.getItem("token"),
      },
      fileList: [],
      formObj: {
        address: "",
        square: "",
        layout: "",
      },
      HouseResource: [
        {
          label: "位置：",
          componentName: "el-cascader",
          propName: "address",
          customClass: "address",
          disabled: false,
          options: [],
        },
        {
          label: "布局：",
          componentName: "el-select",
          childComponentName: "el-radio",
          propName: "layout",
          disabled: false,
          options: [],
        },
        {
          label: "面积：",
          componentName: "el-input",
          propName: "square",
          customClass: "square",
          disabled: false,
          slot: "㎡",
        },
        {
          label: "价格：",
          componentName: "el-input",
          propName: "price",
          disabled: false,
          slot: "¥",
        },
        {
          label: "楼层：",
          componentName: "el-input",
          propName: "level",
          disabled: false,
          slot: "层",
        },
        {
          label: "年份：",
          componentName: "el-input",
          propName: "year",
          disabled: false,
          slot: "年",
        },
      ],
    };
  },
  methods: {
    submitForm() {},
    handleChange() {},
    handleUploadSuccess() {},
    beforeUploadUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isLt2M;
    },
  },
};
</script>

<style lang="scss" scoped>
.release-house-resource {
  margin: 0 auto;
  padding-top: 60px;
}
</style>
<style lang="scss">
.release-house-resource {
  .col-contianer {
    margin: 0 100px;
  }
  .el-upload--picture-card {
    width: 85px;
    height: 35px;
    border: 0;
  }
}
</style>
